export const allButtonThemes = addSourceDecorator(() => {
  const variant = select('Variant', ['default', 'outline', 'flat'], 'default');
  const contrastMode = select(
    'Contrast',
    { Light: 'lightMode', Dark: 'darkMode' },
    'lightMode'
  );

  const { containerStyle, groupStyle, headingStyle } = storyStyle[contrastMode];
  return (
    <div style={containerStyle}>
      <h3 style={headingStyle}>Preset</h3>
      <div style={groupStyle}>
        {themeKeys.map(theme => (
          <Button
            key={`${theme}`}
            style={btnStyle}
            theme={theme}
            outline={variant === 'outline'}
            flat={variant === 'flat'}
          >
            {theme}
          </Button>
        ))}
      </div>
      <h3 style={headingStyle}>Brand</h3>
      <div style={groupStyle}>
        {brandThemeKeys.map(theme => (
          <Button
            key={`${theme}`}
            style={btnStyle}
            theme={theme}
            outline={variant === 'outline'}
            flat={variant === 'flat'}
          >
            {theme}
          </Button>
        ))}
      </div>
      <h3 style={headingStyle}>Deprecated</h3>
      <div style={groupStyle}>
        {deprecatedThemeKeys.map(theme => (
          <Button
            key={`${theme}`}
            style={btnStyle}
            theme={theme}
            outline={variant === 'outline'}
            flat={variant === 'flat'}
          >
            {theme}
          </Button>
        ))}
      </div>
    </div>
  );
}, {__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__});;

